// Node libraries
import * as http from 'http';
import * as url from 'url';

// Project libraries
import * as Util from '@terrencecrowley/util';

// Local libraries
import * as Q from './queue';

export type SQSCallback = (err: any, result: Q.QMessages) => void;

type SQSIndex = { [queueid: string]: SQSQueue };

class SQSRequest
{
  httpoptions: any;
  data: any;
  cb: SQSCallback;
  json: any;
  request: any;
  response: any;
  statusCode: number;
  body: string;

  constructor(httpoptions: any, data: any, cb: SQSCallback)
    {
      this.httpoptions = httpoptions;
      this.data = data;
      this.cb = cb;
      this.body = '';
      this.request = http.request(this.httpoptions, (res: any) => {
          this.response = res;
          this.statusCode = res.statusCode;
          res.setEncoding('utf8');
          res.on('data', (chunk: any) => {
              this.body += chunk;
            });

          res.on('error', (err: any) => {
              this.onError(err);
            });

          res.on('end', () => {
              if (this.statusCode == 200)
              {
                this.json = JSON.parse(this.body);
                this.cb(this.json.error, this.json.result);
              }
              else
                this.onError(`error status: ${this.statusCode}`);
            });
        });

      this.request.on('error', (err: any) => {
          this.onError(err);
        });

      this.request.setHeader('Content-Type', 'application/json; charset=UTF-8');
      this.request.write(JSON.stringify(this.data));
      this.request.end();
    }

  onError(err: any): void
    {
      if (this.cb)
      {
        this.cb(err, null);
        this.cb = null;
      }
    }
}

export class SQSClient
{
  private owner: string;
  private queues: SQSIndex;
  private agent: any;
  private targeturl: any;
  private httpoptions: any;

  constructor(urlstring: string = Q.DefaultServerUrl)
    {
      this.targeturl = new url.URL(urlstring);
      this.owner = Util.createGuid();
      this.queues = {};
      this.agent = new http.Agent( { keepAlive: true } );
      this.httpoptions = {
        protocol: this.targeturl.protocol,
        host: this.targeturl.hostname,
        port: this.targeturl.port ? this.targeturl.port : Q.DefaultPort,
        agent: this.agent,
        method: 'POST',
        path: '/',
        };
    }

  queue(queueid: string): SQSQueue
    {
      let q = this.queues[queueid];
      if (q === undefined)
      {
        q = new SQSQueue(this, queueid);
        this.queues[queueid] = q;
      }
      return q;
    }

  setOptions(queueid: string, options: Q.QQueueOptions, cb: SQSCallback): void
    {
      new SQSRequest(this.httpoptions,
        {
          queueid: queueid,
          api: 'setoptions',
          data: options
        },
        cb);

    }

  send(queueid: string, m: Q.QMessage, cb: SQSCallback): void
    {
      new SQSRequest(this.httpoptions,
        {
          queueid: queueid,
          api: 'send',
          data: m
        },
        cb);
    }

  remove(queueid: string, m: Q.QMessage, cb: SQSCallback): void
    {
      new SQSRequest(this.httpoptions,
        {
          queueid: queueid,
          api: 'remove',
          data: m
        },
        cb);
    }

  receive(queueid: string, cb: SQSCallback): void
    {
      new SQSRequest(this.httpoptions,
        {
          queueid: queueid,
          api: 'receive',
          owner: this.owner,
        },
        cb);
    }
}

export class SQSQueue
{
  private client: SQSClient;
  private queueid: string;

  constructor(client: SQSClient, queueid: string)
    {
      this.client = client;
      this.queueid = queueid;
    }

  setOptions(options: Q.QQueueOptions, cb: SQSCallback): void
    {
      this.client.setOptions(this.queueid, options, cb);
    }

  send(m: Q.QMessage, cb: SQSCallback): void
    {
      this.client.send(this.queueid, m, cb);
    }

  remove(m: Q.QMessage, cb: SQSCallback): void
    {
      this.client.remove(this.queueid, m, cb);
    }

  receive(cb: SQSCallback): void
    {
      this.client.receive(this.queueid, cb);
    }
}
