// Node libraries
import * as http from 'http';
import * as url from 'url';

// Local libraries
import * as Q from './queue';

const LongPollTimeout: number = 5000;

export class SQSServer
{
  private port: number;
  private server: any;
  queuemanager: Q.QQueueManager;
  longpoll: LongPoll;

  constructor(port: number = Q.DefaultPort)
    {
      this.queuemanager = new Q.QQueueManager();
      this.longpoll = new LongPoll();
      this.port = port;

      this.server = http.createServer();

      this.server.on('request', (req: any, res: any) => {
          new OneRequest(this, req, res);
        });

      this.server.on('close', () => {
          this.server = null;
        });

      this.server.on('error', (err: any) => {
          this.server = null;
        });

      try
      {
        this.server.listen(this.port);
      }
      catch (err)
      {
        this.server = null;
      }
    }

  get running(): boolean
    {
      return this.server != null;
    }

  close(): void
    {
      if (this.server)
        this.server.close();
    }
}

interface LongPollRequest
{
  deadline: number;
  request: OneRequest;
}

class LongPoll
{
  requests: LongPollRequest[];

  constructor()
    {
      this.requests = [];
      this.tick = this.tick.bind(this);
      setTimeout(this.tick, 1000);
    }

  check(): void
    {
      let deadline: number = (new Date()).getTime();
      for (let i: number = this.requests.length - 1; i >= 0; i--)
      {
        let r = this.requests[i];
        if (! r.request.onRetry())
          if (r.deadline < deadline)
            r.request.onFinish();
        if (r.request.isDone())
          this.requests.splice(i, 1);
      }
    }

  tick(): void
    {
      this.check();
      setTimeout(this.tick, 1000);
    }

  add(one: OneRequest): void
    {
      this.requests.push({ deadline: (new Date()).getTime() + LongPollTimeout, request: one });
    }
}

class OneRequest
{
  server: SQSServer;
  req: any;
  res: any;
  data: string;
  json: any;
  body: any;

  constructor(server: SQSServer, req: any, res: any)
    {
      this.server = server;
      this.req = req;
      this.res = res;
      this.data = '';
      this.json = null;
      this.body = { statuscode: 0 };
      req.on('data', (s: string) => { this.data += s; });
      req.on('end', () => { this.onDone(); });
      req.on('error', (err: any) => { this.onError(); });
    }

  isDone(): boolean
    {
      return this.res == null;
    }

  onRetry(): boolean
    {
      let result: Q.QMessages = [];
      let qm = this.server.queuemanager;
      qm.receive(this.json.queueid, this.json.owner, result);
      this.body.result = result;
      if (result.length != 0)
      {
        this.onFinish();
        return true;
      }
      else
        return false;
    }

  onDone(): void
    {
      try
      {
        this.json = JSON.parse(this.data);
        if (this.json.queueid === undefined || this.json.queueid == '' || this.json.api === undefined)
        {
          this.onError();
          return;
        }

        let qm = this.server.queuemanager;

        switch (this.json.api)
        {
          case 'setoptions':
            if (this.json.data === undefined)
              this.onError()
            else
              qm.setOptions(this.json.queueid, this.json.data as Q.QQueueOptions);
            break;
          case 'send':
            if (this.json.data === undefined)
              this.onError()
            else if (! qm.send(this.json.queueid, this.json.data as Q.QMessage))
              this.onError();
            else
              this.server.longpoll.check();
            break;
          case 'receive':
            if (this.json.owner === undefined)
              this.onError();
            else
            {
              let result: Q.QMessages = [];
              qm.receive(this.json.queueid, this.json.owner, result);
              this.body.result = result;
              if (result.length == 0 && qm.isLongpoll(this.json.queueid))
              {
                this.server.longpoll.add(this);
                return;
              }
            }
            break;
          case 'remove':
            if (this.json.data === undefined)
              this.onError()
            else if (! qm.remove(this.json.queueid, this.json.data as Q.QMessage))
              this.onError();
            break;
        }

        this.onFinish();
      }
      catch (err)
      {
        this.onError();
      }
    }

  onError(): void
    {
      this.body.statuscode = 1;
      this.body.error = 'failure';
      this.onFinish();
    }

  onFinish(): void
    {
      if (this.res)
      {
        this.res.writeHead(200, { 'Content-Type': 'application/json; charset=UTF-8' });
        this.res.end(JSON.stringify(this.body));
        this.res = null;
      }
    }
}
