export interface OrderedListEntry<T>
{
  prev: OrderedListEntry<T>;
  next: OrderedListEntry<T>;
  seqno: number;
  value: T;
}

export type OrderedListIndex<T> = { [key: string]: OrderedListEntry<T> };

export class OrderedList<T>
{
  head: OrderedListEntry<T>;
  tail: OrderedListEntry<T>;
  next: number;
  index: OrderedListIndex<T>;

  constructor()
    {
      this.head = null;
      this.tail = null;
      this.next = 0;
      this.index = {}
    }

  isempty(): boolean
    {
      return this.head == null;
    }

  clear(): void
    {
      this.head = null;
      this.tail = null;
      this.index = {};
    }

  insert(key: string, value: T): boolean
    {
      if (this.index[key] !== undefined)
        return false;

      let e: OrderedListEntry<T> = { prev: this.tail, next: null, seqno: this.next++, value: value };
      this.tail = e;
      if (this.head == null)
        this.head = e;
      this.index[key] = e;
      return true;
    }

  remove(key: string): boolean
    {
      let e = this.index[key];
      if (e === undefined)
        return false;

      if (e === this.tail)
        this.tail = e.prev;
      else
        e.next.prev = e.prev;

      if (e === this.head)
        this.head = e.next;
      else
        e.prev.next = e.next;

      delete this.index[key];

      return true;
    }

  forEach(cb: (value: T) => boolean): void
    {
      for (let p = this.head; p && cb(p.value); p = p.next)
        continue;
    }
}
